import { DomSanitizer } from "@angular/platform-browser";

import { Component, OnInit, ViewEncapsulation } from "@angular/core";
import { CorrosionService } from "src/app/services/corrosion.service";

@Component({
    selector: "[box-lifetime-ok]",
    templateUrl: "./box-lifetime-ok.component.html",
    styleUrls: ["./box-lifetime-ok.component.scss"],
    encapsulation: ViewEncapsulation.None
})
export class BoxLifetimeOkComponent implements OnInit {
    /*
    data (model)
  */
    status: any = "";
    constructor(
        private corrosionService: CorrosionService,
        private _sanitizer: DomSanitizer
    ) {
        this.status = corrosionService.get("status");
    }
    /*
    component ready (DOM is not ready)
  */
    progress_bar_style: any = "";
    ngOnInit() {
        this.progress_bar_style = this._sanitizer.bypassSecurityTrustStyle(
            `width:${this.status.lifetimeCalculated /
                this.status.lifetimeManufacturer *
                100}%;`
        );
        this.corrosionService.statusUpdated.subscribe(status => {
            this.status = status;
        });
    }
}
